* {
  font-size: 1.2vw;
}

.form {
  display: table;
}
.row {
  font-size: inherit;
  display: table-row;
}
.label {
  display: table-cell;
  padding: 5px 10px 0 0;
  /* color: #FFD8; */
}
.input {
  font: 1.4vw book-antiqua, 'Book Antiqua', serif;
  display: table-cell;
  font-size: 1.2vw;
  background-color: #2D3B6288;
  color: #FFDC;
  border: 1px solid #FFD8;
  border-radius: 5px;
  margin-top: 5px;
  /* outline: 1px solid red; */
}
.input:disabled {
  color: #FFD6;
}
.label:focus, .input:focus {
  color: #FFE;
  outline: none;
  border-color: #FFE;
}
.box, .meanings {
  /* height: calc(100% - 20px); */
  height: 20vw;
  max-width: 30vw;
  overflow: scroll;
  float: left;
  margin: 10px;
  
  padding: 10px;
  background-color: #2D3B6288;
  border: 1px solid #FFD8;
  box-shadow: 0 0 3px #2D3B62FF, 0 0 3px #2D3B62FF inset;
  /* box-shadow: 0 0 5px #FFD8, 0 0 3px #2D3B6200 inset; */
  border-radius: 10px;
}
.meanings {
  max-height: 10vw;
}